.container {
  display: flex;
}
.container .card {
  padding: 10px;
}
.container .card:first-child .card__actions {
  display: flex;
  justify-content: center;
}
.container .card:first-child .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #41e657;
  color: "initial";
}
.container .card:nth-child(2) .card__actions {
  display: flex;
  justify-content: center;
}
.container .card:nth-child(2) .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #800080;
  color: #fff;
}
.container .card:last-child .card__actions {
  display: flex;
  justify-content: center;
}
.container .card:last-child .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #ff0000;
  color: #fff;
}
@media screen and (min-width: 320px) and (max-width: 650px) {
  .container .card {
    background-color: #ff0000;
  }
  .container .card__image {
    width: 30px;
    height: auto;
  }
  .container .card__image .image {
    width: 135px;
    height: auto;
  }
}
@media screen and (min-width: 760px) and (max-width: 1024px) {
  .container .card {
    background-color: #800080;
  }
  .container .card__image {
    width: 235px;
    height: auto;
  }
  .container .card__image .image {
    width: 220px;
    height: auto;
  }
}
@media screen and (min-width: 1030px) and (max-width: 1500px) {
  .container .card {
    background-color: #41e657;
  }
  .container .card__image {
    width: 450px;
    height: auto;
  }
  .container .card__image .image {
    width: 440px;
    height: auto;
  }
}
@media screen and (min-width: 1600px) {
  .container .card {
    background-color: #ffee00;
  }
  .container .card__image {
    width: 820px;
    height: auto;
  }
  .container .card__image .image {
    width: 800px;
    height: auto;
  }
}